import axios from 'axios'; // –Ü–º–ø–æ—Ä—Ç—É—î–º–æ –±—ñ–±–ª—ñ–æ—Ç–µ–∫—É axios –¥–ª—è HTTP-–∑–∞–ø–∏—Ç—ñ–≤

const BASE_URL = 'https://pixabay.com/api/'; // –ë–∞–∑–æ–≤–∞ URL-–∞–¥—Ä–µ—Å–∞ API Pixabay
const API_KEY = '51734453-5d46674fc0c6d7944706aca6e'; // –æ—Å–æ–±–∏—Å—Ç–∏–π –∫–ª—é—á API

/**
 * –í–∏–∫–æ–Ω—É—î –∑–∞–ø–∏—Ç –¥–æ Pixabay API —Ç–∞ –ø–æ–≤–µ—Ä—Ç–∞—î –æ–±'—î–∫—Ç –∑ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞–º–∏.
 * @param {string} query - –ü–æ—à—É–∫–æ–≤–∏–π –∑–∞–ø–∏—Ç
 * @param {number} page - –ù–æ–º–µ—Ä —Å—Ç–æ—Ä—ñ–Ω–∫–∏
 * @param {number} perPage - –ö—ñ–ª—å–∫—ñ—Å—Ç—å –∫–∞—Ä—Ç–∏–Ω–æ–∫ –Ω–∞ —Å—Ç–æ—Ä—ñ–Ω–∫—É (–∑–∞ –∑–∞–º–æ–≤—á—É–≤–∞–Ω–Ω—è–º 15)
 * @returns {Promise<Object>} - –û–±'—î–∫—Ç –∑ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞–º–∏ API { hits, total, totalHits }
 */
export async function getImagesByQuery(query, page = 1, perPage = 15) {
  try {
    const response = await axios.get(BASE_URL, { // –í–∏–∫–æ–Ω—É—î–º–æ GET-–∑–∞–ø–∏—Ç —á–µ—Ä–µ–∑ axios
      params: {
        key: API_KEY,             // –û–±–æ–≤'—è–∑–∫–æ–≤–∏–π –∫–ª—é—á API
        q: query,                 // –ü–æ—à—É–∫–æ–≤–∏–π –∑–∞–ø–∏—Ç (–Ω–∞–ø—Ä–∏–∫–ª–∞–¥, 'cats')
        image_type: 'photo',      // –¢–∏–ø –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è (—Ç—É—Ç —Ñ–æ—Ç–æ)
        orientation: 'horizontal',// –î–æ–¥–∞—Ç–∫–æ–≤–∏–π –ø–∞—Ä–∞–º–µ—Ç—Ä: –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ñ –∫–∞—Ä—Ç–∏–Ω–∫–∏
        safesearch: true,         // –î–æ–¥–∞—Ç–∫–æ–≤–∏–π –ø–∞—Ä–∞–º–µ—Ç—Ä: –±–µ–∑–ø–µ—á–Ω–∏–π –ø–æ—à—É–∫
        page: page,               // –ù–æ–º–µ—Ä —Å—Ç–æ—Ä—ñ–Ω–∫–∏ –ø–∞–≥—ñ–Ω–∞—Ü—ñ—ó
        per_page: perPage,        // –ö—ñ–ª—å–∫—ñ—Å—Ç—å –∫–∞—Ä—Ç–∏–Ω–æ–∫ –Ω–∞ —Å—Ç–æ—Ä—ñ–Ω–∫—É
      },
    });

    return response.data; // –ü–æ–≤–µ—Ä—Ç–∞—î–º–æ –¥–∞–Ω—ñ: { hits, total, totalHits }
  } catch (error) {
    console.error('Error fetching images:', error); // –õ–æ–≥ –ø–æ–º–∏–ª–∫–∏ —É –∫–æ–Ω—Å–æ–ª—ñ
    throw error; // –ü—Ä–æ–±—Ä–æ—Å –ø–æ–º–∏–ª–∫–∏ –¥–∞–ª—ñ, —â–æ–± –º–æ–∂–Ω–∞ –±—É–ª–æ –æ–±—Ä–æ–±–∏—Ç–∏ —ó—ó —É UI
  }
}
