import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";

// –°–µ–ª–µ–∫—Ç–æ—Ä–∏ DOM
const galleryContainer = document.querySelector(".gallery");
const loader = document.querySelector(".loader");
const loadMoreBtn = document.querySelector(".load-more");

// –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è SimpleLightbox
const lightbox = new SimpleLightbox(".gallery a", {
  captionsData: "alt",
  captionDelay: 250,
});

// –ó–º—ñ–Ω–Ω—ñ –¥–ª—è –ø–∞–≥—ñ–Ω–∞—Ü—ñ—ó
let currentPage = 1;
const perPage = 12;

// ------------------- –£—Ç–∏–ª—ñ—Ç–∏ -------------------

/**
 * –î–æ–¥–∞—î –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è —É –≥–∞–ª–µ—Ä–µ—é
 * @param {Array} images
 */
export function createGallery(images) {
  const markup = images
    .map(
      (img) => `
    <a href="${img.largeImageURL}" class="gallery-item">
      <div class="photo-card">
        <img class="photo-card-img" src="${img.webformatURL}" alt="${img.tags}" loading="lazy" />
        <ul class="info">
          <li><b>Likes:</b> ${img.likes}</li>
          <li><b>Views:</b> ${img.views}</li>
          <li><b>Comments:</b> ${img.comments}</li>
          <li><b>Downloads:</b> ${img.downloads}</li>
        </ul>
      </div>
    </a>
  `
    )
    .join("");

  galleryContainer.insertAdjacentHTML("beforeend", markup);
  lightbox.refresh();
}

/**
 * –û—á–∏—â–∞—î –≥–∞–ª–µ—Ä–µ—é
 */
export function clearGallery() {
  galleryContainer.innerHTML = "";
}

/**
 * –ü–æ–∫–∞–∑—É—î —Å–ø—ñ–Ω–µ—Ä
 */
export function showLoader() {
  loader.classList.add("visible");
}

/**
 * –•–æ–≤–∞—î —Å–ø—ñ–Ω–µ—Ä
 */
export function hideLoader() {
  loader.classList.remove("visible");
}

/**
 * –ü–æ–∫–∞–∑—É—î –∫–Ω–æ–ø–∫—É "Load More"
 */
export function showLoadMoreButton() {
  loadMoreBtn.classList.remove("hidden");
}

/**
 * –•–æ–≤–∞—î –∫–Ω–æ–ø–∫—É "Load More"
 */
export function hideLoadMoreButton() {
  loadMoreBtn.classList.add("hidden");
}

/**
 * –ó–∞—Ç—Ä–∏–º–∫–∞ —É –º—ñ–ª—ñ—Å–µ–∫—É–Ω–¥–∞—Ö
 * @param {number} ms
 */
function sleep(ms) {
  return new Promise((resolve) => setTimeout(resolve, ms));
}

// ------------------- –õ–æ–≥—ñ–∫–∞ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è -------------------

async function fetchImages(page = 1) {
  showLoader();
  hideLoadMoreButton();

  try {
    // –†–æ–±–∏–º–æ –∑–∞–ø–∏—Ç –¥–æ API
    const response = await fetch(
      `https://pixabay.com/api/?key=51734453-5d46674fc0c6d7944706aca6e&q=flowers&page=${page}&per_page=${perPage}`
    );
    const data = await response.json();

    // –ú—ñ–Ω—ñ–º–∞–ª—å–Ω–∞ –∑–∞—Ç—Ä–∏–º–∫–∞ 5 —Å–µ–∫—É–Ω–¥ –¥–ª—è —Å–ø—ñ–Ω–µ—Ä–∞
    await sleep(5000);

    return data.hits;
  } catch (error) {
    console.error(error);
    return [];
  } finally {
    hideLoader();
  }
}

async function initGallery() {
  clearGallery();
  currentPage = 1;
  const images = await fetchImages(currentPage);
  createGallery(images);

  if (images.length === perPage) {
    showLoadMoreButton();
  }
}

async function loadMore() {
  currentPage += 1;
  const images = await fetchImages(currentPage);
  createGallery(images);

  if (images.length < perPage) {
    hideLoadMoreButton();
  } else {
    showLoadMoreButton();
  }
}

// –ü–æ–¥—ñ—ó
loadMoreBtn.addEventListener("click", loadMore);

// –ó–∞–ø—É—Å–∫
initGallery();
